header{
    position: sticky;
    top: 0;

    display: flex;
    align-items: center;
    justify-content: space-between;

    color: whitesmoke;
    background-color: #444;

    h1{
        font-size: 1.5rem;
        padding: 1rem 2rem;
    }

    nav.nav-principale{
        display: flex;
        justify-content: flex-start;
        align-items: center;
        margin-right: auto;
        column-gap: 1rem;

        .lien-actif{
            color: yellow;
        }
    }

    nav.nav-secondaire{
        padding: 1rem 2rem;

        display: flex;
        align-items: center;

        #cc-sommaire-panier{
            display: none;
        }
    
        .sommaire-panier{
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            font-size: 1rem;
            background-color: darkgreen;
            position: absolute;
            top: 4rem;
            right: 0;
            transform: translateX(100%);
            transition: transform 0.5s;
    
            h3{
                margin: 0;
                padding: 1rem;
                background-color: black;
                color: white;
            }
    
            div{
                display: flex;
                justify-content: space-between;
                padding: 0.5rem 1rem;
                border-bottom: 1px dotted white;
            }
        }

        #cc-sommaire-panier:checked ~ .sommaire-panier{
            transform: unset;
        }

        label{
            cursor: pointer;
        }

        a{
            padding-left: 0.75rem;
        }
    }
}